<!-- 系统管理 -->
<template>
	<div class="container" >
		<el-row class="row">
			<click-button-add class="button" :listLi="listLi" :title="$t('buttonManage.addTitle')" :rules="rules" :formLabelWidth="formLabelWidth" :addDialog="addDialog" @addConfirm="addConfirm" @addInfoCancel="addInfoCancel" @clickAddInfo="clickAddInfo"></click-button-add>
			<click-button-revise @reviseInfoCancel="reviseCancel" @reviseConfirm="reviseConfirm" @clickReviseInfo="reviseInfo" class="button" :formLabelWidth="formLabelWidth" :listLi="listLi" :title="$t('buttonManage.reviseTitle')" :rules="rules" :info="curNode" :reviseDialog="reviseDialog"></click-button-revise>
			<el-button @click="clickDelete" type="primary" class="button" size="small" icon="el-icon-close">{{ $t('button.delete') }}</el-button>
			<el-row class="model-input">
				<span class="name">按钮名称：</span>
				<el-input  class="input" size="small" v-model="search.buttonName"></el-input>
			</el-row>
			<el-row class="model-input">
				<span class="name">权限标识：</span>
				<el-input class="input" size="small" v-model="search.id"></el-input>
			</el-row>
			<el-button @click="query" type="primary" class="button" size="small" icon="el-icon-search">{{ $t('button.search') }}</el-button>
		</el-row>
		<div ref="tableBox" class="table-box">
			<el-table @select="select" @select-all="selectAll" @row-click="rowClick" ref="table" :data="tableData" :height="tableH" class="center" size="mini" style="text-align:center;width:100%;">
				<el-table-column width="100"  type="selection" height="40px"></el-table-column>
				<el-table-column v-for="(v,index) in $t('buttonManage.tableTitle')" :prop="v.prop" :width="v.width" :label="v.label" :key="index"></el-table-column>
			</el-table>
			<div ref="pagination" class="block">
				<el-pagination class="pagination" :page-size="20" @current-change="changeCurPage" layout="prev, pager, next" :total="total"></el-pagination>
			</div>
		</div>
	</div>	
</template>

<script>
const clickButtonAdd = ()=> import("../../../../components/clickButtonAdd");
const clickButtonRevise = ()=> import("../../../../components/clickButtonRevise");
var ParentFn = ()=>import('../../../../conmon/js/commo.js');
var Parent;
import store from '@/store'
export default {
	components:{
		clickButtonAdd,
		clickButtonRevise
	},
	name: 'buttonManage',
	data() {
		return {
			listLi: {},
			search:{},
			tableData: [],
			tableDataAll: [],
			total: 0,
			tableH: 0,
			addDialog: {
				addDialog: false
			},
			reviseDialog: {
				reviseDialog: false
			},
			formLabelWidth: '120px',
			rules: {},
			curNode: {}
		}
	},
	created() {
		// this.getInfo();$t('buttonManage.listLi')
	},
	mounted() {
		var tbaleBoxH = this.$refs.tableBox.offsetHeight,
		paginationH = this.$refs.pagination.offsetHeight;
		this.tableH = tbaleBoxH - paginationH;
	},
	//keep-alive激活时调用
	activated(){
		this.getInfo();
	},
	methods: {
		//获取初始化的数据
		getInfo(){
			var that = this;
			ParentFn().then(value =>{
				Parent = new value.default.ParentFn();
				//把中英文的参数赋值给Parent
				Parent.error = this.$t('error');
				Parent.success = this.$t('success');
				Parent.button = this.$t('button');
				Parent.message = this.$t('message');
				Parent.buttonManage = this.$t('buttonManage');
				this.listLi = Parent.buttonManage.listLi;
				this.rules = {
					oprCode: [
					{validator: Parent.noIsEmpty(Parent.buttonManage.operationRules.oprCode),required: true, trigger: 'blur' }
					],
					oprName: [
					{validator: Parent.noIsEmpty(Parent.buttonManage.operationRules.oprName), required: true, trigger: 'blur' }
					],
					oprType: [
					{validator: Parent.noIsEmpty(Parent.buttonManage.operationRules.oprType), required: true, trigger: 'blur' }
					]
				};
				Parent.ajax(
					'api/services/app/SmOperation/QueryAll',
					'',
					that,
					(res)=>{
						if(res.success) {
							var list = res.result.items;
							for(var i in list) {
								list[i].checked = false;
							}
							that.tableData = list;
							that.tableDataAll = JSON.parse(JSON.stringify(list));
							that.total = list.length;
						}
					}
					)
			})	
		},
		//当前页码
		changeCurPage(curPage){

		},
		//点击添加按钮
		clickAddInfo(){
			this.addDialog.addDialog = true;
		},
		//添加确认
		addConfirm(res){
			var obj = {};
			var that = this;
			for(var key in res) {
				obj[key] = res[key].model;
			};
			Parent.ajax(
				'api/services/app/SmOperation/Create',
				obj,
				that,
				(res)=>{
					if(res.success) {
						//根据sortNum进行排序
						that.tableDataAll.push(res.result);
						//如果查询没有条件直接添加到表格展示，有值调用查询方法
						if(!that.search.buttonName && !that.search.id) {
							that.tableData.push(res.result);
						} else {
							that.query();
						}
						that.addDialog.addDialog = false;
						that.total = that.tableData.length;
						that.$message({
							type: 'success',
							message: Parent.success.add
						})
					}
				}
				)
		},
		//添加取消
		addInfoCancel(){
			this.addDialog.addDialog = false;
		},
		//点击修改
		reviseInfo(){
			var num = 0;
			var curOperationid;
			for(var i in this.tableData) {
				if(this.tableData[i].checked) {
					this.curNode = this.tableData[i];
					curOperationid = this.tableData[i].id;
					num++;
				}
			}
			//证明选中一条数据
			if(num == 1) {
				for(var key in this.listLi) {//选中修改的参数赋值给弹窗
					this.listLi[key].model = this.curNode[key];
				}
				this.reviseDialog.reviseDialog = true;
			} else {  
				this.$message.error(Parent.error.noData);
			}	
		},
		//点击修改确定
		reviseConfirm(res) {
			var obj = {};
			var that = this;
			for(var key in res) {
				obj[key] = res[key].model;
			};
			obj.id = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmOperation/Update',
				obj,
				that,
				(res)=>{
					if(res.success) {
						for(var i in that.tableData) {
							if(that.tableData[i].id == that.curNode.id) {
								that.tableData.splice(i,1,res.result);
							}
						};
						//当前菜单下所有的模块的数组也要改变
						for(var i in that.tableDataAll) {
							if(that.curNode.id == that.tableDataAll[i].id) {
								that.tableDataAll.splice(i,1,res.result);
							}
						}
						that.curNode = res.result;
						that.reviseDialog.reviseDialog = false;
						//恢复弹框的输入框为初始状态
						for(var key in that.listLi){
							if(that.listLi[key].type == 'dropDown') {
								that.listLi[key].model = 0;
							} else {
								that.listLi[key].model = '';
							}
						}
						that.$message({
							message: Parent.success.revise,
							type: "success"
						});
					}
				},
				"PUT"
				)
		},
		//点击取消
		reviseCancel(){
			this.reviseDialog.reviseDialog = false;
		},
		//选择勾选
		select(selection,row){
			this.curNode = row;
			for(var i in this.tableData) {
				if(this.tableData[i].id == row.id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					if(this.tableData[i].checked) {
						this.$refs.table.setCurrentRow(row);
					} else {
						this.$refs.table.setCurrentRow();
					}
					break;
				}
			}
		},
		//全部勾选
		selectAll(selection){
			this.$refs.table.setCurrentRow();
			if(selection.length > 0) {
				for(var i in this.tableData) {
					this.tableData[i].checked = true;
				}
			} else {
				for(var i in this.tableData) {
					this.tableData[i].checked = false;
				}
			}
		},
		//单击表格row
		rowClick(row, event, column){
			for(var i in this.tableData) {
				if(this.tableData[i].id == row.id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					this.$refs.table.toggleRowSelection(row,this.tableData[i].checked);
					if(this.tableData[i].checked) {
						this.$refs.table.setCurrentRow(row);
					} else {
						this.$refs.table.setCurrentRow();
					}
					break;
				}
			}
		},
		//点击删除数据
		clickDelete(){
			var that = this;
			var curOperationid = [];
			for(var i in this.tableData) {
				if(this.tableData[i].checked) {
					curOperationid.push(this.tableData[i].id);
				}
			}
			if(curOperationid.length > 0) {
				this.$confirm(Parent.message.delete, Parent.message.title, {
					confirmButtonText: Parent.button.confirm,
					cancelButtonText: Parent.button.cancel,
					type: 'warning'
				}).then(() => {
					Parent.ajax(
						'api/services/app/SmOperation/DeleteMore?input=' + curOperationid.join(','),
						'',
						this,
						(res)=>{
							if(res.success) {
								for(var i = 0;i < curOperationid.length;i++) {
									//当前菜单下所有的模块的数组也要改变
									for(var j = 0;j < that.tableDataAll.length;j++) {
										if(curOperationid[i] == that.tableDataAll[j].id) {
											that.tableDataAll.splice(j,1);
											j--;
										}
									};
									//表格展示的数据
									for(var j = 0;j < that.tableData.length;j++) {
										if(curOperationid[i] == that.tableData[j].id) {
											that.tableData.splice(j,1);
											j--;
										}
									};
								}
								that.curNode = {};
								that.total = that.tableData.length;
								that.$message({
									type: "success",
									message: Parent.success.delete
								});
							};
						},
						"DELETE"
						)
				}).catch(() => {

				});
			} else {
				this.$message.error(Parent.error.noData);
			}
		},
		//点击查询的回调
		query(){
			var buttonName = this.search.buttonName,
			id = this.search.id;
			if(buttonName || id) {
				//this.tableDataAll是当前菜单的所以模块
				this.tableData = JSON.parse(JSON.stringify(this.tableDataAll));
				for(var i = 0;i < this.tableData.length;i++) {
					//根据indexOf来判断，这里是两个需要比较的参数
					if(buttonName && id) {
						if(this.tableData[i].oprCode.indexOf(id) == -1 || this.tableData[i].oprName.indexOf(buttonName) == -1) {
							this.tableData.splice(i,1);
							i--;
						}
					} else if(buttonName && !id) {
						if(this.tableData[i].oprName.indexOf(buttonName) == -1) {
							this.tableData.splice(i,1);
							i--;
						} 
					}else if(!buttonName && id) {
						console.log(this.tableData[i].oprCode.indexOf(id))
						if(this.tableData[i].oprCode.indexOf(id) == -1) {
							this.tableData.splice(i,1);
							i--;
						} 	
					}
				}
			} else {
				//为空的时候全部展示出来
				this.tableData = JSON.parse(JSON.stringify(this.tableDataAll));
			}
		}
	},
	store,
	watch:{

	}
}
</script>

<style scoped>
.container{flex: 1;padding:5px 0 0 5px;background:#fff;border-left:#e7e7eb 4px solid;display:flex;flex-direction: column;border: none;}
.content{flex: 1;margin: 0 0 0 5px;background: #fff;display: flex;flex-direction: column;}
.table-box{padding: 10px 5px 0;flex: 1;display: flex;flex-direction: column;}
.block{display: flex;justify-content: center;padding: 40px 0 20px;}
</style>


